/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #F47EDD;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombrePagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcionPagina {
  text-align: center;
}

.horariosPagina {
  text-align: center;
  font-weight: 700;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url(../img/peluqueria.jpg);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
  }
}

.barra {
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton {
  background-color: #CC3D5C;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: #a92d48;
  cursor: pointer;
}

.barraServicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barraServicios {
    display: flex;
    justify-content: space-between;
  }
}
.barraServicios .boton {
  margin: 0 0 1rem 0;
}

.barraServicio {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barraServicio {
    display: flex;
    justify-content: align-items;
  }
}
.barraServicio .boton {
  margin: 0 0 1rem 0;
}

.barraServicios1 {
  margin: 0px !important;
  padding: 0px !important;
}
@media (min-width: 768px) {
  .barraServicios1 {
    display: flex;
    justify-content: space-between;
  }
}
.barraServicios1 .boton {
  margin: 0px;
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .textCenter {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.tabla_horas {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(9, 1fr);
}

.tabla_horas li {
  outline: 2px solid black;
  background-color: green;
  text-align: center;
  list-style: none;
}

.tabla_horas li.red {
  background-color: red;
}

.tabla_horas li.green {
  background-color: green;
}

.listadoServicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listadoServicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listadoServicios .servicio {
  background-color: #1a1b15;
  padding: 3rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
}
.listadoServicios .servicio.seleccionado {
  background-color: #8F22BB;
}
.listadoServicios .servicio.seleccionado p {
  color: #FFFFFF;
}
.listadoServicios .servicio:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.listadoServicios .servicio p {
  color: #FFFFFF;
  margin: 0 0 1.25rem 0;
}
.listadoServicios .servicio .precioServicio {
  color: #0da6f3;
  font-weight: 700;
  margin: 0;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #72CB10;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedorServicio {
  padding: 3rem 0;
  border-bottom: 1px solid #e1e1e1;
}
.contenedorServicio:last-of-type {
  margin-bottom: 5rem;
}

.citas, .servicios {
  list-style: none;
  padding: 0;
}
.citas li, .servicios li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
}
.citas li p, .servicios li p {
  font-weight: 700;
  color: #72CB10;
}
.citas li span, .servicios li span,
.citas li .servicio,
.servicios li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}
.citas li:hover, .servicios li:hover {
  background-color: #8F22BB;
}

.servicios .acciones {
  margin: 0;
}
.servicios .boton,
.servicios .botonDelete {
  margin: 0;
}

.totalCitas p {
  color: #CC3D5C;
  font-weight: 700;
}
.totalCitas span {
  color: #FFFFFF;
  font-weight: 400;
}

.formulario {
  margin-top: 5rem;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 10rem;
}
.campo input:disabled {
  background-color: white;
  color: #1a1b15;
  cursor: not-allowed;
}
.campo select {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 10rem;
}
.campo select:disabled {
  background-color: white;
  color: #1a1b15;
  cursor: not-allowed;
}
.campo h4 {
  color: black;
  border: 2px solid;
  border-radius: 10px;
  padding: 10px;
  background-color: #f80000;
}

.paginacion {
  margin-top: 5rem;
  margin-bottom: 3rem;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .paginacion {
    justify-content: flex-end;
  }
}
.paginacion__enlace {
  color: #1a1b15;
  font-size: 2rem;
}
.paginacion__enlace--texto {
  padding: 1rem;
  border-radius: 2rem;
  border: 2px solid #1a1b15;
}
.paginacion__enlace--texto:hover {
  background-color: #CC3D5C;
  color: #FFFFFF;
}
.paginacion__enlace--numero {
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--numero {
    display: inline;
  }
}
.paginacion__enlace--actual {
  color: #CC3D5C;
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--actual {
    display: inline;
  }
}

.boton {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: black;
  cursor: pointer;
}

.botonDelete {
  background-color: #f80000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .botonDelete {
    width: auto;
  }
}
.botonDelete:hover {
  background-color: #c50000;
  cursor: pointer;
}

.botonReporte {
  display: flex;
  width: 250px !important;
  border-radius: 2rem;
  margin: 0px !important;
  background-color: #3CFAB0;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .botonReporte {
    width: auto;
  }
}
.botonReporte:hover {
  background-color: #0af99c;
  cursor: pointer;
}
@media (min-width: 768px) {
  .botonReporte {
    display: flex;
    justify-content: align-items;
  }
}
.botonReporte .boton {
  margin: 0 0 1rem 0;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.acciones a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #f80000;
}
.alerta.exito {
  background-color: #72CB10;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #8F22BB;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms ease;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #6e1a90;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #8F22BB;
}

.paginacion1 {
  padding: 5rem 0;
}
@media (min-width: 480px) {
  .paginacion1 {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion1 button {
  margin: 0 0 1rem 0;
}

.ocultar {
  visibility: hidden;
}